.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}

.black-nav{
  background-color: black;
  color:white;
  text-align: left;
  padding-left: 20px;
  padding-top:1px;
  height: 70px;
  width: 100%;
}

.container {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-items: center;
  align-items: start;
  background-color: antiquewhite;
}

.list{
  padding: 10px;
  height: 120px;
  width: 70%;
  border: 1px solid goldenrod;
  margin: 10px;
  background-color: whitesmoke;
  box-shadow: 2px 2px 3px grey;
}

.list:hover {
  transform: translateY(-5px);
}

.list h4 {
  cursor: pointer;
}

.list span {
  margin-left: 10px;
  cursor: pointer;
}

.save-box {
  width: 100%;
  padding: 10px;
}

#save {
  padding : 5px;
  margin-left:10px;
}

.modal {
  background-color: whitesmoke;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px grey;
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translate(-50%, -50%);
  z-index: 10;
  width: 80%;
  max-width: 500px;
}